import React, { useMemo, useState } from 'react';
import { MainMap, Filter, RouteList } from '../../components';
import './index.scss';
import { allRoutes, getFilteredRoute } from '../../constants/route';

const Home: React.FC = () => {
  const prefix = 'home';
  const [filterForm, setFilterForm] = useState<FilterForm>({
    region: '',
    range: [],
  });
  const filteredList = useMemo(() => getFilteredRoute(allRoutes, filterForm), [filterForm]);

  return (
    <div className={`${prefix}`}>
      <Filter
        className={`${prefix}-filter`}
        value={filterForm}
        onChange={setFilterForm}
      />
      <RouteList
        className={`${prefix}-list`}
        routes={filteredList}
      />
      <MainMap
        className={`${prefix}-map`}
        routes={filteredList}
      />
    </div>
  );
};

export default Home;
